@import "../variables/variables";
@import "../mixins/font";
@import "reset";
@import "fonts";
@import "img";


.g-container {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	height: 100%;
	background: rgba(0, 0, 0, 0.0001);
}

body {
	padding: 0;
	margin: 0;
	font-family: $font_family;
	font-size: 14px;
	color: $main_color_text;
}
.wrapper{
	max-width: 1170px;
	margin: 0 auto;
	@media screen and (max-width:940px) {
		max-width: 768px;
		width: 100%;
	 }
	 @media screen and (max-width:768px) {
		max-width: 768px;
		width: 100%;
	 }
	 
}


// start
// header start

	.header__menu-list{
	 display: flex;
	 align-items: center;
	 justify-content: space-between;
	 
	margin: 45px 178px 0 178px;
	 max-width: 100%;
	 @media screen and (max-width:940px) {
		max-width: 940px;
		margin: 41px 80px 0 80px;
	 }
	
	
 
		 .header__list-item-link {
			&:hover{
				color: $color_link_hover;
			}
			color:$main_color_title;
		 letter-spacing: 1.1px;
		 text-transform: uppercase;
		 font-weight: 400;
		 font-size: 11px;
		 line-height: 22px;
		 }
	 }
	 
	 
 
// header end

// main start
.main__content{
	margin-top: 51px;
	@media screen and (max-width:940px) {
		margin-top: 43px;
	 }
}
.main__content-img{
    width: 100%;
	@media screen and (max-width:940px) {
		
	 }
}
.main__content-our{
	max-width: 1165px;
	margin-top: 66px;
	h2{
		font-weight: 700;
        font-size: 16px;
        line-height: 22px;
        color: $main_color_title;
        text-transform: uppercase;
	}
}
.our-images{
	border: 1px solid black;
	width: 100%;
	margin-top: 24px;
	display: flex;
}
